import { useEffect, useRef } from "react";
import useEventBus from "../../hooks/useEventBus";

const PopupInfo = () => {
	const { buffer } = useEventBus();

	return (
		<div className="min-w-[240px] pt-2 px-2">
			<h2 className="text-center font-bold text-xl mb-4">Основні характеристики</h2>
			<Label name={'середнє значення інтенсивності'} value={buffer.meanIntensity} />
			<Label name={'середнє значення квадрата відхилення'} value={buffer.contrast} />
			<Label name={'середній інформаційний зміст '} value={buffer.entropy} />
			<Histogram value={buffer.histogram} />
		</div>
	);
}


const Label = ({ name, value }) => {
	return name && (
		<div className="flex justify-between mb-2">
			<span className="mr-2">{name} = </span>
			<span>{value}</span>
		</div>
	);
}

const Histogram = ({ value = [] }) => {
	const canvasRef = useRef(null);

	useEffect(() => {
		const canvas = canvasRef.current;
		const context = canvas.getContext('2d');
		const totalPixels = Math.max(...value);

		var barWidth = canvas.width / (value.length);
		var barHeight = canvas.height / (totalPixels);

		var begin = true;

		context.clearRect(0, 0, canvas.width, canvas.height);
		context.fillStyle = 'black';

		context.beginPath();
		for (let i = 1; i < value.length; i++) {
			if(value[i] < 1)
				continue;
			if(begin) {
				begin = false;
				context.moveTo(i * barWidth, canvas.height - value[i] * barHeight);
			} else context.lineTo(i * barWidth, canvas.height - value[i] * barHeight);
		}
		context.stroke();
	})

	return (<canvas className="w-full h-full bg-gray-300" ref={canvasRef} />);
}

export default PopupInfo;